a {
    text-decoration: none;
    transition: .3s color, .3s background, .3s opacity, .3s box-shadow;
    color: inherit;

    &:hover {
        text-decoration: none;
    }
}

.main-nav a {
    color: $clr1;
}

.shop-nav a {
    color: $clr4;
}

.brand-link:hover {
    color: $clr1;
}