// Font Sizes
.small-font {
    font-size: 0.8rem;
}

.medium-font {
    font-size: 1.1rem;
}

.large-font {
    font-size: 1.8rem;
}

// List
.clean-list {
    margin: 0;
    padding: 0;
    list-style-type: none;

    &>li {
        margin: 0;
        padding: 0;
    }
}

.bolded {
    font-weight: 700;
}

.blurred {
    filter: blur(4px);
}

// Positioning
.abs-tl {
    position: absolute;
    top: 0;
    left: 0;
}

.flex {
    display: flex;

    >*.full {
        flex: 1;
    }

    &.column {
        flex-direction: column;
    }

    &.justify-center {
        justify-content: center;
    }

    &.justify-end {
        justify-content: end;
    }

    &.align-center {
        align-items: center;
    }

    &.wrap {
        flex-wrap: wrap;
    }

    &.space-between {
        justify-content: space-between;
    }

    &.space-around {
        justify-content: space-around;
    }

    &.auto-center {
        justify-content: center;
        align-items: center;
    }
}

.text-center {
    text-align: center;
}

.center-block {
    margin: 0 auto;
}

// Media helpers
.square-ratio {
    @include aspect-ratio(1, 1);
}

.wide-ratio {
    @include aspect-ratio(16, 9);
}

.square-3 {
    @include aspect-ratio(4, 3);
}

// Buttons
.simple-button {
    background: transparent;
    color: black;
    font-size: inherit;
    font-family: inherit;
    border: 0;
    opacity: 0.5;
    transition: 0.3s all;
    padding: 0;
    cursor: pointer;

    &:disabled {
        cursor: initial;
        opacity: 0.2;
    }

    i {
        display: inline-block;
        width: 100%;
        height: 100%;
    }

    &:hover:not(:disabled) {
        opacity: 1;
    }
}

.medium-button {
    font-size: 1.5rem;
}

// Transitions
.fast-trans {
    transition: 0.25s all;
}

.normal-trans {
    transition: 0.4s all;
}

.slow-trans {
    transition: 0.7s all;
}

// Spacing
.p5 {
    padding: 5px !important;
}

.p10 {
    padding: 10px !important;
}

.p20 {
    padding: 20px !important;
}

.m5 {
    margin: 5px !important;
}

.m10 {
    margin: 10px !important;
}

.m20 {
    margin: 20px !important;
}

.simple-form {
    display: flex;
    flex-direction: column;
    width: 300px;
    margin: 15px auto;

    &>* {
        margin-bottom: 10px;
    }

    input:not([type="checkbox"]),
    button {
        padding: 5px 8px;
        width: 100%;
    }
}

//background normalize
.normBackground {
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}

.in-spacer-horiz-10 {
    &>*:not(:last-child) {
        margin-right: 10px;
    }
}

.in-spacer-vert-10 {
    &>*:not(:last-child) {
        margin-bottom: 10px;
    }
}

.bottom-divider {
    padding-bottom: 15px;
    margin-bottom: 15px;
    border-bottom: 1px solid lightgray;
}